import '../sass/main.scss';
import makeAnimated from 'react-select/animated';
import Select from 'react-select'
import { Query } from "react-apollo";
import ApolloClient from "apollo-boost";
import { ApolloProvider } from "react-apollo";
import { AllBrandsQuery, GroupsQuery } from '../queries/tauto.queries';
import { prepareArraySelect } from '../helpers';

const customStyles = {
  input: (base) => ({
    ...base,
    maxWidth: '20px',
  }),
  valueContainer: (base) => ({
    ...base,
    minHeight: '50px',
  })
}

export default class PricesForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = { brand: '', model: '' };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  client = new ApolloClient({
    uri: "https://api.miautohoy.com/graphql"
  });
  handleChange(field, value) {
    this.setState({ [field]: value });
  }

  handleSubmit(event) {
    alert('Your favorite flavor is: ' + this.state.value);
    event.preventDefault();
  }
  render() {
    return (
      <ApolloProvider client={this.client}>
        <div className="container text-center form-container">
          <Query
            query={AllBrandsQuery}
          >
            {({ loading, error, data }) => {
              if (loading) return <p>Loading...</p>;
              if (error) return <p>Error :(</p>;

              return <form onSubmit={this.handleSubmit}>
                <label className="mb-4">
                  Completa los datos para obtener el precio del veh√≠culo
                <Select
                  onChange={({ value }) => this.handleChange('brand', value)}
                  placeholder="Selecciona la marca"
                  components={makeAnimated()}
                  options={prepareArraySelect(data.AllBrands, 'ta3_nmarc', 'ta3_marca')} styles={customStyles} />
                  </label>
                <Query query={GroupsQuery} variables={{ gru_nmarc: this.state.brand }}>
                  {({ loading, error, data }) => {
                    if (loading) return <p>Loading...</p>;
                    if (error) return <p>Error :(</p>;
                      return <Select
                      onChange={({ value }) => this.handleChange('model', value)}
                      placeholder="Selecciona el grupo"
                      components={makeAnimated()}
                      options={prepareArraySelect(data.Group, 'gru_cgrup', 'gru_ngrup')} styles={customStyles} />
                    }}
                </Query>
                    <button type="submit" className="btn-primary" value="Submit">Ver precio</button>
              </form>
            }}
          </Query>
        </div>
      </ApolloProvider>

    );
  }
}